header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 45px;
    nav {
        flex-grow: 1;
        margin-left: 30px;
        ul {
            display: flex;
            align-items: center;
            gap: 30px;
            list-style: none;
            padding: 0;
            margin: 0;
            li {
                a {
                    text-decoration: none;
                    font-size: 17px;
                    font-weight: 500;
                    text-transform: uppercase;
                    color: #606060;
                }
            }
            li:hover {
                a {
                    font-weight: 700;
                    color: rgb(226, 51, 51);
                }
            }
        }
        
    }
    .button {
        margin-left: auto;
        margin-right: 0;
        background-color: #141416;
        color: #fff;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        border-radius: 16.17px;
        padding: 16px 24px;
        background: rgb(20, 20, 22);
        cursor: pointer;
        text-decoration: none;
    }
    .button:hover {
        background-color: rgb(226, 51, 51);
        transition: .2s ease-out;
    }
}
.cur a{
    color: rgb(226, 51, 51);
    font-family: Inter;
    font-size: 17.83px;
    font-weight: 700;
    line-height: 26.74px;
    letter-spacing: 0%;
    text-align: left;
    text-transform: uppercase;
}